/* General - https://codex.wordpress.org/CSS#WordPress_Generated_Classes
   ========================================================================== */
.alignright {
    float: right;
    margin-left: 1rem; }

.alignleft {
    float: left;
    margin-right: 1rem; }

.aligncenter {
    clear: both;
    display: block;
    margin-left: auto;
    margin-right: auto; }

.pagination {
    margin: 20px auto; }

.sticky {
    position: relative;
    display: block; }

.bypostauthor {
    font-size: inherit; }

.gallery-caption {
    display: block;
    font-size: 0.8125rem;
    line-height: 1.5;
    margin: 0;
    padding: 0.75rem; }

.wp-caption {
    margin-bottom: 1.25rem;
    max-width: 100%; }
.wp-caption.alignleft {
    margin: 5px 20px 20px 0; }
.wp-caption.alignright {
    margin: 5px 0 20px 20px; }
.wp-caption img {
    display: block;
    margin-left: auto;
    margin-right: auto; }

.wp-caption-text {
    margin: 0; }

.screen-reader-text {
    clip: rect(1px, 1px, 1px, 1px);
    height: 1px;
    overflow: hidden;
    position: absolute !important;
    width: 1px;
    word-wrap: normal !important;
    /* Many screen reader and browser combinations announce broken words as they would appear visually. */ }
.screen-reader-text:focus {
    background-color: #eeeeee;
    clip: auto !important;
    -webkit-clip-path: none;
    clip-path: none;
    color: #333333;
    display: block;
    font-size: 1rem;
    height: auto;
    left: 5px;
    line-height: normal;
    padding: 15px 23px 14px;
    text-decoration: none;
    top: 5px;
    width: auto;
    z-index: 100000;
    /* Above WP toolbar. */ }